<template>
	<view class="body">
		
		<view class="my-config">
			<view class="config-item">
				<view>头像</view>
				<view class="config-header">
					<image :src="userImg" class="userImg"></image>
					<view class="config-mark">></view>
				</view>
			</view>
			<view class="config-item">
				<view>会员名称:{{username}}</view>
				<view class="config-mark">></view>
			</view>
			<view class="config-item">
				<view>性别</view>
				<view class="config-mark">></view>
			</view>
		</view>
		
		<view class="my-config">
			<view class="config-item">
				<view>手机号码:{{phone}}</view>
				<view class="config-mark">></view>
			</view>
			<view class="config-item">
				<view>修改支付密码</view>
				<view class="config-mark">></view>
			</view>
			<view class="config-item">
				<view>修改登录密码</view>
				<view class="config-mark">></view>
			</view>
		</view>
		
		<view class="login-out" @click="loginOut">退出登录</view>
		
	</view>
</template>

<script>
	import {mapActions} from 'vuex'
	export default {
		data() {
			return {
				userImg:'http://43.139.55.209:8081/api/resources/image?name=',
				username:'',
				phone:''
			}
		},
		methods: {
			loginOut(){
				uni.removeStorageSync('token'),
				uni.removeStorageSync('userinfo')
				uni.reLaunch({
					url:"/pages/my/my"
				})
			}
		},
		onLoad() {
			let obj=uni.getStorageSync('userinfo')
			this.userImg='http://43.139.55.209:8081/api/resources/image?name='
			this.userImg+=obj.icon
			this.username=obj.username
			this.phone=obj.phone
		}
	}
</script>

<style scoped>
.body{
	background-color: whitesmoke;
	width: 100%;
	height: 100vh;
}
.my-config{
	padding-bottom: 50rpx;
	margin-bottom: 50rpx;
	border-radius: 30rpx;
	background-color: white;
}
.config-item{	
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: 20rpx 0 20rpx 20rpx;
	border-bottom: 2rpx solid lightgray;
	line-height: 80rpx;
}
.config-header{
	display: flex;
	justify-content: center;
	align-items: center; 
}
.userImg{
	width: 80rpx;
	height: 80rpx;
	background-color: gray;
	border: 2px solid #FFFFFF;
	border-radius: 50%;
}
.config-mark{
	padding-right: 40rpx;
}
.login-out{
	background-color: white;
	width: 100%;
	line-height: 100rpx;
	color: black;
	border-radius: 30rpx;
	text-align: center;
	font-weight: bold;
}
</style>
